<template lang="pug">
    div.blackList-content
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            :top-status.sync="topStatus"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            template(v-if="isLoaded")
                ul(v-if="list && list.length > 0")
                    li.list(v-for="(item,index) in list")
                        div.head-img
                            router-link(:to="{ path : '/user/others' , query : { to_user_id : item.user_id }}")
                                img(v-lazy="item.head_image")
                        div.user-information
                            div.user-title
                                span(v-text="item.nick_name")
                                div.praise
                                    i.icon.iconfont.sex-man(v-if="item.sex == 1") &#xe661;
                                    i.icon.iconfont.sex-woman(v-else) &#xe662;
                                    img.icon-level(:src="iconLevel[index]")
                            div.no-way
                                img(src="../../../assets/img/icon-black.png")
                    <div v-if="allLoaded" class="no-more-data">无更多数据</div>
                null-data(v-else)  
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
</template>
<script>
import nullData from '@/components/nullData'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { Toast } from 'mint-ui'
export default {
    name: 'settingBlacklist',
    components: {
        nullData,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            list: '',
            isLoaded: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: false,
            topStatus: '',
            bottomStatus: ''

        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh = false) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/settingsBlacklist' : this.api.settingsBlacklist();
            this.axios.get(axiosUrl).then(res => {
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.list = res.data.user;
                    this.page = 2;
                    this.has_next = res.data.has_next;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
                this.$nextTick(function() {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });

            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                })
        },
        loadTop(id) {
            this.getData(true);
        },
        loadBottom() {
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/settingsBlacklist' : this.api.settingsBlacklist();
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                })
                    .then(res => {
                        setTimeout(() => {
                            if (res.data.status == 1) {
                                self.has_next = res.data.has_next;
                                self.page = self.page + 1;
                                if (res.data.user && res.data.user.length && res.data.user.length > 0) {
                                    for (let i = 0; i < res.data.user.length; i++) {
                                        self.list.push(res.data.user[i]);
                                    }
                                }
                            }
                            else {
                                Toast(res.data.error || '请求出错');
                            }
                            self.$refs.loadmore.onBottomLoaded();
                        }, 500)
                    })
                    .catch(err => {
                        Toast(err || '网络异常');
                        self.$refs.loadmore.onBottomLoaded();
                    })
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            this.topStatus = status;
        },
        handleBottomChange(status) {
            this.bottomStatus = status;
        }
    },
    watch: {
        list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_level}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.blackList-content {
    ul {
        background-color: #fff;
        padding-left: .5rem;
        .list {
            height: 3.25rem;
            width: 100%;
            display: flex;
            align-items: center;
            position: relative;
            .head-img {
                height: 2rem;
                width: 2rem;
                border-radius: 2.5rem;
                overflow: hidden;
                flex-shrink: 0;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .user-information {
                border-bottom: 1px solid #e6e8f1;
                width: 100%;
                height: 3.25rem;
                margin-left: .5rem;
                font-size: .65rem;
                display: flex;
                text-align: center;
                white-space: nowrap;
                .user-title {
                    padding-top: .75rem;
                    display: flex;
                    align-items: baseline;
                    width: 50%;
                    span {
                        font-size: .75rem;
                        color: @fc-main;
                        display: block;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .praise {
                        .iconfont {
                            font-size: .75rem;
                            margin: 0 .25rem;
                            line-height: 1;
                        }
                        .sex-woman {
                            color: @color-red;
                        }
                        .sex-man {
                            color: @color-blue;
                        }
                        img {
                            width: 1.3rem;
                            height: .63rem;
                            vertical-align: text-top;
                        }
                    }
                }
                .no-way {
                    height: 2rem;
                    width: 2rem;
                    position: absolute;
                    right: .5rem;
                    top: 50%;
                    margin-top: -1rem;
                    img {
                        width: 100%;
                        height: 100%;
                        transform: rotateX(40deg);
                    }
                }
            }
        }
    }
}
</style>



